import * as React from 'react';
import classes from './typography.module.scss';
import classnames from 'classnames';

type Props = {
  tag?: 'h1' | 'h2' | 'h3' | 'h4' | 'h5' | 'h6' | 'p';
  className?: string;
} & React.Props<{}>;

export const Typography = (props: Props) => {
  const Component = props.tag || 'p';
  const extraClasses: { [key: string]: boolean, } = {};
  if (props.className) {
    extraClasses[props.className] = true;
  }
  return (
    <Component
      className={classnames(classes.typography, extraClasses)}
    >
      {props.children}
    </Component>
  );
};
