import * as React from 'react';
import { Grid } from '../../../common/atoms/grid';
import { IProfile, IEducation } from '../../../@types/profile/profile';
import { Typography } from '../../../common/atoms/typography';
import classes from './education.module.scss';

const EducationHistory = ({ education }: { education: IEducation }) => {
  return (
    <Grid container>
      <div className={classes.educationHistoryPrimary}>
        <Typography tag="p">{education.degreeName}</Typography>
        <Typography tag="p">{education.institutionName} - {education.location}</Typography>
      </div>
      <div className={classes.educationHistorySecondary}>
        <Typography tag="p">{education.startDate} - {education.endDate}</Typography>
      </div>
    </Grid>
  );
};

export const Education = ({ profile }: { profile: IProfile }) => {
  return (
    <Grid container className={classes.container}>
      <Grid xs={12} md={4}>
        <Typography tag="h2">Education</Typography>
      </Grid>
      <Grid xs={12} md={8}>
        {profile.education.map(education => (
          <EducationHistory
            key={education.institutionName}
            education={education}
          />
        ))}
      </Grid>
    </Grid>
  );
};
