import * as React from 'react';
import classes from './app-bar.module.scss';
import { Grid } from '../../atoms/grid';
const Logo = require('./ec-logo.png');

interface IMenuItem {
  text: string;
  to: string;
}

const menuItems: IMenuItem[] = [{
  text: 'Profile',
  to: '#profile',
}, {
  text: 'Portfolio',
  to: '#portfolio',
}, {
  text: 'Education',
  to: '#education',
}, {
  text: 'Employment',
  to: '#employment-history',
}, {
  text: 'Skills and Availability',
  to: '#skills-availability',
}]

const AppBarItem = ({ item, }: { item: IMenuItem; }) => {
  const handleClick = (e: React.MouseEvent) => {
    e.preventDefault();
    const element = document.getElementById(item.to.replace('#', ''));
    element?.scrollIntoView({ behavior: 'smooth' });
  }
  return (
    <div className={classes.appBarItem}>
      <a href={item.to} onClick={handleClick}>{item.text}</a>
    </div>
  )
};

export const AppBar = (props: React.Props<{}> & { showLinks: boolean }) => {
  return (
    <div className={classes.container}>
      <div className={classes.buttonSlot} style={{ backgroundImage: `url(${Logo})`}} />
      <Grid container justify="center" className={classes.navItems}>
        {props.showLinks && menuItems.map(item => (
          <AppBarItem
            item={item}
          />
        ))}
      </Grid>
      <div className={classes.buttonSlot} />
    </div>
  );
};
