import * as React from 'react';
import { useState, useRef, useEffect } from 'react';
import classnames from 'classnames';

export const LoadableSkeleton = ({ className, src, onSelect }: { className?: string, src: string, onSelect?: (img: string) => void }) => {
  const [ loaded, setLoaded ] = useState(false);
	const [ failed, setFailed ] = useState(false);
	const imageRef = useRef<HTMLImageElement>(null);
	useEffect(() => {
    setTimeout(() => {
      setLoaded(true);
    }, 3000);
	}, [src])
  return (
    <React.Fragment>
      {(loaded || failed) ? null : <div className="skeleton-item skeleton-borderless"><div className={classnames('skeleton-picture', className)}/></div>}
      <img
        style={!loaded ? { visibility: 'hidden', position: 'absolute' } : {}}
        src={src}
				className={className}
				ref={imageRef}
        onLoad={() => setLoaded(true)}
        onError={() => setFailed(true)}
				onClick={() => onSelect ? onSelect(src) : null}
				alt=""
      />
    </React.Fragment>
  );
};