import * as React from 'react';
import { Grid } from '../../../common/atoms/grid';
import { IProfile, IExpertise } from '../../../@types/profile/profile';
import { Typography } from '../../../common/atoms/typography';
import classes from './skills-summary.module.scss';
import { Tag } from '../../../common/atoms/tag/tag.component';

const ExpertiseRow = ({ expertise }: { expertise: IExpertise }) => {
  const yearsText = expertise.expYears > 1 ? `${expertise.expYears} years experience` : `${expertise.expYears} year experience`;
  return (
    <Grid container className={classes.expertiseRow} wrap="nowrap">
      <Grid container alignItems="center">
        <img alt="" aria-hidden src={expertise.logo} />
      </Grid>
      <div>
        <Grid container alignItems="flex-end">
          <Typography tag="h3">{expertise.name}</Typography>
          <Typography tag="p">{yearsText}</Typography>
        </Grid>
        <Typography tag="p">{expertise.description}</Typography>
      </div>
    </Grid>
  );
};

export const SkillsSummary = ({ profile }: { profile: IProfile }) => {
  return (
    <Grid container className={classes.container}>
      <Grid xs={12} md={4}>
        <Typography tag="h2">Expertise</Typography>
        {profile.experience.expertise.map(expertise => (
          <ExpertiseRow key={expertise.name} expertise={expertise} />
        ))}
      </Grid>
      <Grid xs={12} md={4}>
        <Typography tag="h2">Skills</Typography>
        {profile.experience.skills.map(skill => (
          <Tag key={skill.name}>
            {skill.name}
          </Tag>
        ))}
      </Grid>
      <Grid xs={12} md={4} className={classes.availabilityBlock}>
        <Typography tag="h2">Availability</Typography>
        <Typography tag="p" className={classes.timezone}>{profile.location.timezone}</Typography>
        <Typography tag="h3" className={classes.languagesTitle}>Languages</Typography>
        <Grid container className={classes.languagesBlock}>
          {profile.location.languages.map(language => (
            <Tag key={language}>{language}</Tag>
          ))}
        </Grid>
        {/* <Typography tag="h3">Location</Typography>
        <iframe
          title="Location"
          width="600"
          height="450"
          frameBorder="0"
          src={`https://www.google.com/maps/embed/v1/place?key=AIzaSyAvPAvx7cvbginaI6FPvqfwL8m6HhUdbJA&q=${profile.location.address}`}
        >
        </iframe> */}
      </Grid>
    </Grid>
  );
};
