import * as React from 'react';
import { Grid } from '../../../common/atoms/grid';
import classes from './employment-history.module.scss';
import { IProfile, IPortfolio } from '../../../@types/profile/profile';
import { Typography } from '../../../common/atoms/typography';
import { IconLabel } from '../../../common/molecules/icon-label/icon-label.component';
import { Tag } from '../../../common/atoms/tag/tag.component';

type Props = {
  profile: IProfile;
}

const EmploymentDetails = ({ details }: { details: IPortfolio }) => {
  return (
    <div className={classes.employmentDetails}>
      <Grid container direction="row" alignItems="center">
        <Grid item xs="auto" container direction="column">
          <Typography tag="h3">{details.title}</Typography>
          <Grid container direction="row" className={classes.subDetails}>
            <Typography tag="p">{details.company}</Typography>
            <Typography tag="p">{details.startDate} - {details.endDate}</Typography>
          </Grid>
        </Grid>
        <img alt="Company Logo" aria-hidden className={classes.companyLogo} src={details.companyLogo} />
      </Grid>
      <ul>
        {details.responsibilities.map(responsibility => (
          <li>{responsibility}</li>
        ))}
      </ul>
      <Grid container direction="row" className={classes.technologyDetails}>
        {details.tools.map(tech => <Tag>{tech}</Tag>)}
      </Grid>
    </div>
  );
};

export const EmploymentHistory = (props: Props) => {
  return (
    <Grid container className={classes.container}>
      <Grid xs={12} md={4}>
        <IconLabel
          icon={null}
          text="Employment History"
        />
      </Grid>
      <Grid xs={12} md={8}>
        {
          (props.profile.experience.portfolio.map(details => (
            <EmploymentDetails details={details} />
          )))
        }
      </Grid>
    </Grid>
  )
}