import * as React from 'react';
import { useState } from 'react';
import classes from './hero-layout.module.scss';
import { AppBar } from '../../molecules/app-bar/app-bar.component';
import { Footer } from '../../molecules/footer/footer.component';
import { HeroLayoutContext } from './hero-layout.context';

export const AppBarHeroLayout = (props: React.Props<HTMLDivElement>) => {
  const [contentLoaded, setContentLoaded] = useState<boolean>(false);
  return (
    <HeroLayoutContext.Provider value={{ setContentLoaded, }}>
      <div className={classes.container}>
        <AppBar showLinks={contentLoaded} />
        <div className={classes.contentOuter}>
          <div className={classes.contentInner}>
            {props.children}
          </div>
        </div>
        <Footer />
      </div>
    </HeroLayoutContext.Provider>
  );
};