import * as React from 'react';
import { useContext } from 'react';
import { Grid } from '../../../common/atoms/grid';
import { IProfile, IPortfolio } from '../../../@types/profile/profile';
import { Typography } from '../../../common/atoms/typography';
import classes from './portfolio.module.scss';
import { ProfileContext } from '../profile.context';

const PortfolioItem = ({ portfolio }: { portfolio: IPortfolio }) => {
  const { setModalPortfolio } = useContext(ProfileContext);
  const handleOpenPortfolio = () => {
    setModalPortfolio(portfolio);
  };
  return (
    <div className={classes.portfolioItem}>
      <button className={classes.portfolioThumbnail} style={{ backgroundImage: `url(${portfolio.thumbnailUrl})`}} onClick={handleOpenPortfolio}>
        <div className={classes.portfolioThumbnailOverlay}>
          <Typography tag="p">{portfolio.title}</Typography>
        </div>
      </button>
    </div>
  );
};

export const Portfolio = ({ profile }: { profile: IProfile }) => {
  return (
    <Grid container className={classes.container}>
      <Grid xs={12} md={4}>
        <Typography tag="h2">Portfolio</Typography>
      </Grid>
      <Grid xs={12} md={8} container>
        {profile.experience.portfolio.map((portfolio) => (
          <Grid xs={12} md={6}>
            <PortfolioItem portfolio={portfolio} key={portfolio.title} />
          </Grid>
        ))}
      </Grid>
    </Grid>
  );
};
