import * as React from 'react';
import { useState } from 'react';
import classes from './portfolio-content.module.scss';
import { Grid } from '../../../common/atoms/grid';
import { Typography } from '../../../common/atoms/typography';
import { IProfile, IPortfolio, IPortfolioGalleryItem } from '../../../@types/profile/profile';
import classnames from 'classnames';
import { LoadableImage } from '../../../common/molecules/loadable-image/loadable-image.component';

const PortfolioMedia = (props: { item?: IPortfolioGalleryItem }) => {
	if (!props.item) {
		return null;
	}
	if (props.item.videoUrl) {
		return (
			<iframe
				title={props.item.subtitle}
				width="560"
				height="315"
				src={props.item.videoUrl}
				frameBorder="0"
				allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
				allowFullScreen
			/>
		);
	}
	return  <LoadableImage className={classes.gallerySelected} src={props.item.screenshot} />;
}

export const PortfolioContent = ({
	profile, portfolio, onClick
}: { profile: IProfile, portfolio: IPortfolio | null | undefined, onClick: () => void }) => {
	const [selectedGalleryItemIndex, setGalleryItem] = useState(0);
	const selectedGalleryItem = portfolio?.galleryItems[selectedGalleryItemIndex];
	return (
		<Grid container direction="column" className={classes.modal}>
			<Grid container className={classes.modalBar}>
				<Grid item container>
					<img className={classes.profileLogo} src={profile?.personal?.photoUrl} alt="profile" />
					<div className={classes.modalBarProfileInfo}>
						<Typography tag="p">{profile?.personal?.firstName} {profile.personal?.lastName}</Typography>
						<Typography tag="p">{profile?.location?.city}, {profile?.location?.state}, {profile?.location?.country}</Typography>
					</div>
				</Grid>
				<Grid xs="auto" />
				<div>
					<button className={classes.modalButtonClose} onClick={onClick}>
						<svg xmlns="http://www.w3.org/2000/svg" width="30" height="30">
						  <g fillRule="nonzero" stroke="#8F96A3" fill="none">
						    <g strokeLinecap="square">
						      <path d="M9.08 20.92L20.73 9.27M20.92 20.92L9.27 9.27l11.65 11.65z"/>
						    </g>
						    <path d="M.5.5h29v29H.5z"/>
						  </g>
						</svg>
					</button>
				</div>
			</Grid>
			<Grid container xs="auto" className={classes.modalContent}>
				<Grid item xs={12} sm={6} md={8} container wrap="nowrap" className={classes.modalContentScreenshots} >
					<Grid container className={classes.modalContentScreenshotsNavigation}>
						{
							portfolio?.galleryItems.map((item, i) => (
								<button
									key={`${item.subtitle}-${i}`}
									className={classnames(classes.galleryButton, {
										[classes.galleryButtonActive]: selectedGalleryItemIndex === i
									})}
									onClick={() => setGalleryItem(i)}
								>
									<LoadableImage src={item.thumbnail} />
								</button>
							))
						}
					</Grid>
					<div className={classes.gallerySelectedContainer}>
						<PortfolioMedia item={selectedGalleryItem} />
					</div>
				</Grid>
				<Grid item xs={12} sm={6} md={4} className={classes.modalContentDescription}>
					<Typography tag="h2">{portfolio?.title}</Typography>
					<div dangerouslySetInnerHTML={{ __html: `${portfolio?.description}` }} />
				</Grid>
			</Grid>
		</Grid>
	);
};
