import * as React from 'react';
import { useState, useRef, useEffect } from 'react';
import classes from './loadable-image.module.scss';

const LoadingIndicator = ({ className } :{ className?: string}) => {
	return (
		<div className={className}>
			<div className={classes.loadingIndicator}>
				<p>Loading</p>
			</div>
		</div>
	);
};

export const LoadableImage = ({ className, src, onSelect }: { className?: string, src: string, onSelect?: (img: string) => void }) => {
  const [ loaded, setLoaded ] = useState(false);
	const [ failed, setFailed ] = useState(false);
	const imageRef = useRef<HTMLImageElement>(null);
	useEffect(() => {
		setLoaded(false);
	}, [src])
  return (
    <React.Fragment>
      {(loaded || failed) ? null : <LoadingIndicator className={className} />}
      <img
        style={!loaded ? { visibility: 'hidden', position: 'absolute' } : {}}
        src={src}
				className={className}
				ref={imageRef}
        onLoad={() => setLoaded(true)}
        onError={() => setFailed(true)}
				onClick={() => onSelect ? onSelect(src) : null}
				alt=""
      />
    </React.Fragment>
  );
};