import * as React from 'react';
import classes from './personal-summary.module.scss';
import { Grid } from '../../../common/atoms/grid';
import { Typography } from '../../../common/atoms/typography';
import { IProfile } from '../../../@types/profile/profile';
import { Tag } from '../../../common/atoms/tag/tag.component';
import { LoadableSkeleton } from '../../../common/molecules/loadable-skeleton/loadable-skeleton.component';

type Props = {
  profile: IProfile;
} &  React.Props<{}>;

export const PersonalSummary = ({ profile }: Props) => {
  return (
    <Grid container>
      <Grid item xs={12} md={4} className={classes.profilePhoto}>
        <LoadableSkeleton className={classes.profilePhotoImage} src={profile.personal.photoUrl} />
        {/* <img alt="Photo" aria-hidden src={profile.personal.photoUrl} /> */}
      </Grid>
      <Grid item xs={12} md={8} className={classes.personalBlock}>
        <Typography tag="h2" >
          {`${profile.personal.firstName} ${profile.personal.lastName}`}
        </Typography>
        <Grid direction="row" className={classes.personalMainPoints}>
          <Typography tag="p">{profile.experience.profession}</Typography>
          <Typography tag="p">{profile.location.city}, {profile.location.state}, {profile.location.country}</Typography>
        </Grid>
        <div className={classes.personalMainSummary} dangerouslySetInnerHTML={{ __html: profile.personal.summary }}></div>
        <Grid item container direction="row" className={classes.skillsContainer}>
          {profile.experience.skills.map(skill => (
            <Tag>{skill.name}</Tag>
          ))}
        </Grid>
      </Grid>
    </Grid>
  );
};
