import * as React from 'react';
import { Switch, Route, Redirect, Router } from 'react-router';
import { AppBarHeroLayout } from './common/layouts/hero-layout/hero-layout.component';
import { NotFound } from './pages/not-found/not-found';
import { Profile } from './pages/profile/profile';
import { SourceCode } from './pages/source-code/source-code';
import { createBrowserHistory } from 'history';

const history = createBrowserHistory();

const App = (props: React.Props<{}>) => {
  return (
    <AppBarHeroLayout>
      <Router history={history}>
        <Switch >
          <Route exact path="/" component={Profile} />
          <Route exact path="/source-code" component={SourceCode} />
          <Route exact path="/not-found" component={NotFound} />
          <Redirect to="/not-found" />
        </Switch>
      </Router>
    </AppBarHeroLayout>
  )
}

export default App;
