import * as React from 'react';
import { useContext, useState, useEffect } from 'react';
import { PersonalSummary } from './personal-summary/personal-summary.component';
import { EmploymentHistory } from './employment-history/employment-history.component';
import { Portfolio } from './portfolio/portfolio';
import { SkillsSummary } from './skills-summary/skills-summary';
import { Education } from './education/education';
import { ProfileContext } from './profile.context';
import Popup from 'reactjs-popup';
import { IPortfolio, IProfile } from '../../@types/profile/profile';
import { PortfolioContent } from './portfolio-content/portfolio-content';
import { Skeleton } from '../../common/atoms/skeleton/skeleton.component';
import { ContentPanel } from '../../common/atoms/content-panel/content-panel.component';
import { HeroLayoutContext } from '../../common/layouts/hero-layout/hero-layout.context';

function getParameterByName(name: string, url: string) {
  if (!url) url = window.location.href;
  name = name.replace(/[[\]]/g, '\\$&');
  var regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)'),
      results = regex.exec(url);
  if (!results) return null;
  if (!results[2]) return '';
  return decodeURIComponent(results[2].replace(/\+/g, ' '));
}

export const Profile = (props: React.Props<{}>) => {
  const context = useContext(HeroLayoutContext);
  const [ isLoading, setLoading ] = useState<boolean>(true);
  const [ profile, setProfile ] = useState<IProfile | null | undefined>();
  const [ modalPortfolio, setModalPortfolio ] = useState<IPortfolio | null | undefined>();

  useEffect(() => {
    setLoading(true);
    const profileId = getParameterByName('profileId', window.location.href);
    switch (profileId) {
      case '0':
      default:
        import('../../profile-data/mikedao').then((module) => {
          setProfile(module.default);
          setLoading(false);
          context.setContentLoaded(true);
        });
        break;
    }
  }, [context]);
  if (isLoading) {
    return (
      <Skeleton />
    );
  }
  if (!profile) {
    return null;
  }
  return (
    <ContentPanel>
      <ProfileContext.Provider value={{ setModalPortfolio }}>
        <span id="profile" />
        <PersonalSummary key="personal" profile={profile} />
        <span id="education" />
        <Education key="education" profile={profile} />
        <span id="employment-history" />
        <EmploymentHistory key="employment" profile={profile} />
        <span id="portfolio" />
        <Portfolio key="portfolio" profile={profile} />
        <span id="skills-availability" />
        <SkillsSummary key="skills-summary" profile={profile} />
        <Popup
          open={!!modalPortfolio} closeOnEscape={false} closeOnDocumentClick={false}
          contentStyle={{
            width: '100%',
            height: '100%',
            backgroundColor: 'transparent',
            border: 0,
            padding: 0,
          }}
        >
          <PortfolioContent profile={profile} portfolio={modalPortfolio} onClick={() => setModalPortfolio(null)} />
        </Popup>
      </ProfileContext.Provider>
    </ContentPanel>
  );
};
